export default {
  name: "links",
  data() {
    return {
      links_list: undefined
    }
  },
  created() {
    this.init();
    this.listener_init = this.$hub.on('event:init', () => {
      this.init();
      this.$forceUpdate();
    });
  },
  methods: {
    init: function () {
      console.log("link_conf:", this.$store.state.init.data.link_conf);
      this.links_list = this.$store.state.init.data.link_conf;
    }
  },
  destroyed() {
    this.listener_init.off();
  },
}
